import React, { lazy, useEffect, useState } from 'react'
import { useMutation, useLazyQuery } from '@apollo/client';
import { Button, Modal, message, Pagination } from 'antd';
import { DeleteTwoTone, PlusCircleOutlined, EditTwoTone, UsergroupAddOutlined } from '@ant-design/icons';
import { Scrollbars } from 'react-custom-scrollbars';
import { Link } from "react-router-dom";
import moment from 'moment';
import Flex from 'components/shared-components/Flex';
import { ALL_ONLINE_LESSON, DELETE_ONLINE_LESSON } from 'graphql/lesson';
import IntlMessage from 'components/util-components/IntlMessage';
import { CalendarIcon } from '@heroicons/react/outline';
import { APP_PREFIX_PATH } from 'configs/AppConfig';
import Loading from 'components/shared-components/Loading';
import userImage from 'assets/image/user.png'
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';

const LessonForm = lazy(() => import('./lesson-form'));

const Lessons = props => {

    const [list, setList] = useState([]);
    const [count, setCount] = useState(1);

    const { confirm } = Modal;

    const [fetchData, { loading, refetch }] = useLazyQuery(ALL_ONLINE_LESSON, {
        nextFetchPolicy: 'network-only',
        onCompleted: data => {
            setCount(data.count.count)
            setList(data.allOnlineLessons)
        }
    });

    const [deleteLesson] = useMutation(DELETE_ONLINE_LESSON, {
        onCompleted: data => {
            refetch();
            message.success('Амжилттай устлаа');
        }
    });

    useEffect(() => {
        fetchData({ variables: { offset: 0, limit: 10, filter: '' } })
    }, [fetchData])

    const [isModalVisible, setIsModalVisible] = useState(false);
    const [editData, setEditData] = useState([]);
    const [formType, setFormType] = useState("");

    const showModal = () => {
        setFormType("create")
        setIsModalVisible(true);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const editRow = values => {
        setEditData(values);
        setFormType("edit")
        setIsModalVisible(true);
    };

    const deleteModal = value => {
        confirm({
            title: 'Устгах уу?',
            okText: 'Устгах',
            okType: 'danger',
            cancelText: 'Болих',
            onOk() {
                deleteLesson({ variables: { id: value } });
            },
        });
    }

    const triggerFetch = (page, pageSize) => {
        var start = 0;
        var end = pageSize;
        if (page !== 1) {
            start = (page * pageSize) - pageSize
            end = pageSize * page
        }
        fetchData({ variables: { offset: start, limit: (end - 1), filter: '' } })
    }

    const onPageChange = (page, pageSize) => {
        triggerFetch(page, pageSize);
    }

    const dropdownMenu = row => (
        <div className='w-full flex flex-col space-y-2 bg-background p-2 rounded-2' onClick={e => (e && e.stopPropagation) && e.stopPropagation()}>
            {props.permissions.edit === true &&
                <Button onClick={() => editRow(row)}>
                    <EditTwoTone twoToneColor={'#ffdb00'} />
                </Button>
            }
            {props.permissions.view_student === true &&
                <Link
                    to={`/app/online-lesson-students/${row.id}`}
                >
                    <Button>
                        <UsergroupAddOutlined />
                    </Button>
                </Link>
            }
            {props.permissions.destroy === true &&
                <Button onClick={() => deleteModal(row.id)} >
                    <DeleteTwoTone twoToneColor={'#f42f2f'} />
                </Button>
            }
        </div>
    );

    if (loading) {
        return <Loading />
    }

    return (
        <div>
            {props.permissions.create === true &&
                <>
                    <Modal
                        forceRender
                        width={'80vw'}
                        visible={isModalVisible}
                        okText={<IntlMessage id="main.okText" />}
                        cancelText={<IntlMessage id="main.cancelText" />}
                        onCancel={handleCancel}
                        okButtonProps={{ form: 'LessonForm', key: 'submit', htmlType: 'submit' }}
                    >
                        <LessonForm formType={formType} setIsModalVisible={setIsModalVisible} editData={editData} />
                    </Modal>
                    <Flex justifyContent="end" alignItems="center" className="py-2">
                        <div>
                            <Button
                                style={{ float: 'right' }}
                                size='small'
                                type="primary"
                                onClick={() => showModal()}
                            >
                                <PlusCircleOutlined />  <IntlMessage id="add_new" />
                            </Button>
                        </div>
                    </Flex>
                </>
            }

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6 w-full mt-4">
                {list.map((lesson, index) => (
                    <Link
                        to={`${APP_PREFIX_PATH}/online-lesson/${lesson.id}`}
                        key={index}
                        className={`border-regular border-1 pt-[20px] px-[25px] pb-[25px] bg-white rounded-10 bg-cover bg-center`}
                    >
                        <div className="flex items-center justify-between">
                            <h4
                                className={`text-[16px] font-medium items-center mb-[14px] flex gap-[10px]`}
                            >
                                <span>{lesson.subject.subject}</span>
                            </h4>
                            <EllipsisDropdown menu={dropdownMenu(lesson)} />
                        </div>
                        <div>
                            <Scrollbars style={{ height: 200 }}>
                                <p className={`mb-[20px] leading-[1.786] whitespace-pre-line`}>
                                    {lesson.description}
                                </p>
                            </Scrollbars>
                        </div>
                        <div className="flex flex-wrap items-center justify-between">
                            <div className="flex items-center gap-[15px]">
                                <img className="w-[30px] h-[30px] rounded-full" src={userImage} alt="" />
                                <span className={`text-[15px] font-medium `}>{lesson.createUserid.teacher?.name}</span>
                            </div>
                            <div className="flex items-center gap-[15px]">
                                <CalendarIcon className='h-7 text-emind' />
                                <p>{moment(lesson.createdAt).format('YYYY-MM-DD')}</p>
                            </div>
                        </div>
                    </Link>
                ))}
            </div>
            {count > 0 && (
                <Pagination
                    className='text-right mt-4'
                    total={count}
                    onChange={onPageChange}
                />
            )}
        </div>
    )

}

export default Lessons
